.main{
    padding: 2rem 0;
    width: 100%;
    height:100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}
.main .box{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
    padding: 4rem 0.50rem;
    flex-wrap: wrap;
}
.main .box .title {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
    text-align: center;
}
.main .box .title h1{
    font-size: 52px;
    font-weight: 700;
    color: var(--color-1);
}
.main .box .title h2{
    font-size:36px;
    font-weight: 700;
    color: var(--color-2);
}
.main .box .title p {
    font-size: 20px;
}

.main > img{
    width: 80px;
    object-fit: cover;
    animation: arriba 0.50s ease-in-out alternate infinite;
    cursor: pointer;
}
@keyframes arriba{
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(20px);
    }
}