.section{
    display: flex;
    flex-direction: column;
    gap: 40px;
    padding: 3rem 2rem;
    text-align: center;
    max-width: 800px;
    margin: auto;
}
.section h2{
    font-size: 46px;
    font-weight: 700;
    margin-bottom: 1rem;
    color: var(--color-1);
}
.section p{
    font-size: 20px;
    line-height: 1.5;
}
.section.skill .skill__img{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
}