.header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
    box-shadow: 0px 0px 10px rgba(255,255,255,0.25);
    font-weight: 500;
    gap: 25px;
}
.header ul{
    display: flex;
    list-style: none;
    gap: 5px;
    flex-wrap: wrap;
}
.header ul li{
    font-size: 18px;
    border-radius: 12px;
    padding: 0.5rem 1rem;
    cursor: pointer;
}
.header ul li:hover{
    color: var(--color-1);
}