.porfolio {
    width: 100%;
    max-width: 100%;
}

.porfolio .projects{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: center;
    align-items: flex-start;
}
.porfolio .projects h3 {
    font-size: 28px;
}
.porfolio .projects p{
    font-size: 16px;
    text-align: center;
}
.porfolio .projects .project {
    padding: 1.5rem;
    background-color: var(--black);
    max-width: 500px;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    box-shadow: 1px 1px 3px rgba(255, 255, 255, 1);
    
}
.porfolio .projects img {
    width: 100%;
    height: auto;
    border-radius: 12px;
}
.porfolio .projects a {
    text-decoration: none;
    color: var(--black);
}
.btn{
    font-size: 18px;
    font-weight: 700;
    background-color: var(--white);
    padding: 16px 20px;
    border-radius: 12px;
}